function approveInventory(formData, id) {
  try {
    return async (dispatch) => {
      const data = await Connector.put(`/inventories/${id}/approval`, formData);
      console.log(data);
      notify("Successfully approved inventory");
      reloadPage();
    };
  } catch (err) {
    notify("failed to update");
  }
}

